@import url("https://fonts.googleapis.com/css?family=Roboto:300,400&display=swap");
* {
  box-sizing: border-box;
}
body {
  font-family: "Roboto", sans-serif;
  color: #707070;
  /* background-color: #f1f1f1; */
  margin: 0;
}
.container {
  min-height: 100vh;
  max-height: 100vh;
}

.item-container {
  padding: 50px 20px;
  background-color: #40b1a2;
  display: grid;
  row-gap: 30px;
  width: 400px;
  justify-items: center;
}

.splash {
  position: absolute;
  font-size: 120px;
  left: 8%;
  bottom: -5%;
  opacity: 0.15;
  user-select: none;
  z-index: 1;
}

.page-head {
  display: grid;
  grid-template-columns: repeat(3, 100px);
  grid-template-rows: repeat(4, auto);
  grid-gap: 10px 10px;
  padding: 30px 20px 10px 20px;
  background-color: #40b1a2;
  text-align: center;
  color: #fff;
  border-color: #fff;
  position: relative;
  overflow: hidden;
}

.class-info {
  background-color: #ffc55c;
}
.page-head > a {
  text-align: left;
}

.page-head > *:not(.splash) {
  z-index: 2;
}

.expand {
  grid-column: 1 / -1;
}
.page-head > .leader {
  text-align: left;
}

.page-head p {
  margin: 0;
}

.page-head > .letter {
  margin: 0 auto;
}

.page-head > ul {
  text-align: left;
}
.page-head > ul > li {
  display: inline-block;
  text-align: left;
  margin-right: 15px;
}
.page-head > div > .title,
.page-head > div > .title + .item-info {
  margin-bottom: 5px;
}

.page-head > a > .button-small {
  width: 100%;
}
.item-list > ul {
  overflow-y: scroll;
  max-height: 300px;
}

.item-list > .sub-title {
  margin-left: 40px;
}

.title {
  font-size: 21px;
  color: inherit;
}

.sub-title {
  font-size: 16px;
  color: inherit;
}

.item-name,
.body-text {
  font-size: 12px;
  color: inherit;
}

.item-info {
  font-size: 12px;
  font-weight: 300;
  color: inherit;
}

.light-font {
  font-weight: 300;
}

.nav {
  display: grid;
  grid-template-columns: repeat(2, auto);
  /* padding: 0 40px; */
  color: #fff;
}
.nav > *:first-child {
  justify-self: start;
  display: inline;
}
.nav > *:last-child {
  justify-self: end;
  text-decoration: none;
  color: #fff;
}
.nav > *:only-child {
  justify-self: start;
  display: inline;
}
.nav > *:first-child,
.nav > *:only-child {
  justify-self: start;
  display: inline;
}

.logo {
  width: 94px;
}
.floating-label-wrapper {
  width: 80%;
}
.floating-label-field {
  width: 100%;
}

.floating-label-field,
.floating-label-wrapper {
  position: relative;
}

.floating-label-field {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #707070;
}
.floating-label-field::placeholder {
  color: transparent;
}

.floating-label-field:focus {
  outline: 0;
  box-shadow: 0;
}

.floating-label-field,
.floating-label-field + .floating-label {
  line-height: 1;
  font: inherit;
  padding: 10px 5px 5px 10px;
}

.floating-label-field + .floating-label {
  position: absolute;
  top: 0;
  left: 0;

  transition: transform 0.2s, opacity 0.2s ease-in-out;
  transform: 0 0;
  opacity: 0.5;
}

.floating-label-field:focus + .floating-label,
.floating-label-field:not(:placeholder-shown) + .floating-label {
  transform: translate(0, -50%) scale(0.8);
  opacity: 0.25;
}

.button {
  border: 1px solid #40b1a2;
  background-color: rgba(255, 255, 255, 0);
  color: #40b1a2;
  font-size: 16px;
  text-transform: uppercase;
}

.button:hover {
  border-color: #40b1a2;
  background-color: #40b1a2;
  color: #fff;
}

.button-inverse {
  border: 1px solid #fff;
  background-color: rgba(255, 255, 255, 0);
  color: #fff;
  font-size: 16px;
  text-transform: uppercase;
}

.button-inverse:hover {
  border-color: #fff;
  background-color: #fff;
  color: #40b1a2;
}
.button-large {
  min-height: 40px;
  min-width: 300px;
  max-height: 40px;
  max-width: 300px;
}

.button-small {
  min-height: 25;
  min-width: 70px;
  font-size: 9px;
  padding: 1em;
}

.letter {
  max-width: 50px;
  min-width: 50px;
  min-height: 50px;
  max-height: 50px;
  border: 2px solid;
  border-color: inherit;
  color: inherit;
  border-radius: 50%;
  text-align: center;
  line-height: 44px;
}

.letter span {
  font-size: 28px;
  vertical-align: middle;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.list-item {
  background-color: #fff;
  margin-bottom: 1px;
  padding: 15px 0 15px 0;
  max-height: 60px;
  min-width: 100%;

  display: grid;
  grid-template-rows: repeat(2, auto);
  grid-template-columns: 32px 200px;
  column-gap: 20px;
  color: #707070;
  border-color: #707070;
  justify-content: center;
}

ul > a {
  text-decoration: none;
}

.list-item > .list-item-name {
  font-size: 16px;
  color: inherit;
}

.list-item > .list-item-info {
  font-size: 12px;
  font-weight: 300;
  color: inherit;
}

.list-item > .list-item-initials {
  max-width: 32px;
  max-height: 32px;
  border-radius: 50%;
  border: 2px solid;
  border-color: inherit;
  background-color: transparent;

  margin: auto 0;
  line-height: 30px;

  grid-row: 1 / -1;
  grid-column: 1 / 2;
  text-align: center;
  color: inherit;
}

.list-item:hover {
  background-color: #40b1a2;
  color: #fff;
  border-color: #fff;
}

.attendance,
.missing,
.study {
  margin-bottom: 15px;
}
.attendance {
  color: #40b1a2;
  background-color: transparent;
  border: 1px solid;
  border-color: #40b1a2;
}

.attendance:hover,
.selected {
  background-color: #40b1a2;
  color: #fff;
  border-color: #fff;
}

.missing {
  color: #f85968;
  background-color: transparent;
  border: 1px solid;
  border-color: #f85968;
}

.missing:hover,
.missing-selected {
  background-color: #f85968;
  color: #fff;
  border-color: #fff;
}

.study {
  color: #ffc55c;
  background-color: transparent;
  border: 1px solid;
  border-color: #ffc55c;
}

.study:hover,
.study-selected {
  background-color: #ffc55c;
  color: #fff;
  border-color: #fff;
}

.form-grid {
  height: calc(100vh - 250px);
  display: grid;
  row-gap: 20px;
  justify-items: center;
  align-content: center;
}

.login-page > .form-grid > .logo {
  margin-bottom: 20px;
}

.login-page > .form-grid {
  grid-template-rows: auto auto auto auto;
}

.login-page > .form-grid > *:last-child {
  align-self: end;
}

.member {
  display: grid;
  grid-template-rows: repeat(4, auto);
}

.error {
  margin: 0 auto;
  color: #f00;
  border-color: #f00;
}
.error:hover {
  color: #fff;
  background-color: #f00;
}
.center {
  margin: 0 auto;
}

.attendance-head {
  text-align: center;
}

.attendance-head > p {
  margin: 0;
}

.attendance-grid {
  display: grid;
  grid-template-rows: auto auto 1fr;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px 20px;
  height: calc(100vh - 250px);
  padding: 30px 20px 30px 20px;
}

.attendance-grid > .next {
  grid-column: 2;
}

.attendance-grid > .prev {
  grid-column: 1;
}

.attendance-grid > .button {
  max-height: 40px;
  min-height: 40px;
  align-self: end;
}

.nav > a > .menu {
  color: #707070;
  width: 16px;
}

.dropdown {
  position: relative;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #fff;
  min-width: 100px;
  z-index: 1;
}

.dropdown-content button {
  color: #707070;
  padding: 12px 16px;
  border: 1px solid #40b1a2;
  background-color: rgba(255, 255, 255, 0);
  font-size: 16px;
  text-transform: uppercase;
}

.dropdown-content button:hover {
  background-color: #40b1a2;
  color: #fff;
}
.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .menu {
  fill: #40b1a2;
}

.form-page {
  padding: 30px 20px 0 20px;
}

.form-page .title {
  text-align: center;
}
.form-page .letter {
  margin: 0 auto;
}

.form-page .menu {
  width: 16px;
}

.profile-page {
  background-color: #f1f1f1;
}

.empty-list {
  text-align: center;
}
.item-list > .sub-title {
  display: inline-block;
  margin-right: 50px;
}

.item-list > input,
.text-input {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #707070;
}

.add-existing > .item-name {
  margin-bottom: 20px;
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position: fixed;
  background: white;
  padding: 30px;
  border-radius: 4px;
  width: 300px;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-main > .modal-grid {
  display: grid;
  grid-template-columns: repeat(2, auto);
  column-gap: 20px;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
